//引用mSearch组件，如不需要删除即可
import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
export default {
	data() {
		return {
			defaultKeyword: "",
			keyword: "",
			oldKeywordList: [],
			hotKeywordList: [],
			keywordList: [],
			forbid: '',
			isShowKeywordList: false
		}
	},
	onLoad() {
		this.init();
		
	},
	components: {
		//引用mSearch组件，如不需要删除即可
		mSearch
	},
	methods: {
		init() {
			this.loadDefaultKeyword();
			this.loadOldKeyword();
			this.loadHotKeyword();

		},
		blur(){
			uni.hideKeyboard()
		},
		//加载默认搜索关键字
		loadDefaultKeyword() {
			//定义默认搜索关键字，可以自己实现ajax请求数据再赋值,用户未输入时，以水印方式显示在输入框，直接不输入内容搜索会搜索默认关键字
			this.defaultKeyword = "搜索热门目的地/景点";
		},
		//加载历史搜索,自动读取本地Storage
		loadOldKeyword() {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) =>{
					console.log(res)
					var OldKeys =  JSON.parse(res.data);
					// var OldKeys = res.data?res.data:[];
					this.oldKeywordList = OldKeys;
				}
			});
		},
		//加载热门搜索
		loadHotKeyword() {
			//定义热门搜索关键字，可以自己实现ajax请求数据再赋值
			this.hotKeywordList = ['热门搜索1', '热门搜2', '热门搜索3', '热门搜索4', '热门搜索5'];
		}, 
		//监听输入
		inputChange(event) {
			//兼容引入组件时传入参数情况
			var keyword = event.detail?event.detail.value:event;
			if (!keyword) {
				this.keywordList = [];
				this.isShowKeywordList = false;
				return;
			}
			this.isShowKeywordList = true;
			//以下示例截取淘宝的关键字，请替换成你的接口
			uni.request({
				url: 'https://suggest.taobao.com/sug?code=utf-8&q=' + keyword, //仅为示例
				success: (res) => {
					this.keywordList = [];
					this.keywordList = this.drawCorrelativeKeyword(res.data.result, keyword);
					
				}
			});
		},
		//高亮关键字
		drawCorrelativeKeyword(keywords, keyword) {
			var len = keywords.length,
				keywordArr = [];
			for (var i = 0; i < len; i++) {
				var row = keywords[i];
				//定义高亮#9f9f9f
				var html = row[0].replace(keyword, "<span style='color: #000000;font-weight:bold'>" + keyword + "</span>");
				html = '<div>' + html + '</div>';
				var tmpObj = {
					keyword: row[0],
					htmlStr: html
				};
				keywordArr.push(tmpObj)
			}
			return keywordArr;
		},
		Toresults(){
			uni.navigateTo({
				url:"../searchResults/searchResults"
			})
		},
		//顶置关键字
		setKeyword(index) {
			this.keyword = this.keywordList[index].keyword;
		},
		//清除历史搜索
		oldDelete() {
			uni.showModal({
				content: '确定清除历史搜索记录？',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.oldKeywordList = [];
						uni.removeStorage({
							key: 'OldKeys'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		//热门搜索开关
		hotToggle() {
			this.forbid = this.forbid ? '' : '_forbid';
		},
		//执行搜索
		doSearch(keyword) {
			keyword = keyword===false?this.keyword:keyword;
			this.keyword = keyword;
			this.saveKeyword(keyword); //保存为历史 
			uni.showToast({
				title: keyword,
				icon: 'none',
				duration: 2000
			});
			//以下是示例跳转淘宝搜索，可自己实现搜索逻辑
			/*






			*/
		},
		//保存关键字到历史记录
		saveKeyword(keyword) {
			const value =  uni.getStorageSync('OldKeys');
				if (value) {
					console.log(res)
					var OldKeys = JSON.parse(res.data);
					// var OldKeys = res.data;
					var findIndex = OldKeys.indexOf(keyword);
					if (findIndex == -1) {
						OldKeys.unshift(keyword);
					} else {
						OldKeys.splice(findIndex, 1);
						OldKeys.unshift(keyword);
					}
					//最多10个纪录
					OldKeys.length > 10 && OldKeys.pop();
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				}
				// key: 'OldKeys',
				// success: (res) => {
				// 	console.log(res)
				// 	var OldKeys = JSON.parse(res.data);
				// 	// var OldKeys = res.data;
				// 	var findIndex = OldKeys.indexOf(keyword);
				// 	if (findIndex == -1) {
				// 		OldKeys.unshift(keyword);
				// 	} else {
				// 		OldKeys.splice(findIndex, 1);
				// 		OldKeys.unshift(keyword);
				// 	}
				// 	//最多10个纪录
				// 	OldKeys.length > 10 && OldKeys.pop();
				// 	uni.setStorage({
				// 		key: 'OldKeys',
				// 		data: JSON.stringify(OldKeys)
				// 	});
				// 	this.oldKeywordList = OldKeys; //更新历史搜索
				// },
				// fail: (e) => {
				// 	var OldKeys = [keyword];
				// 	uni.setStorage({
				// 		key: 'OldKeys',
				// 		data: JSON.stringify(OldKeys),
				// 		success:(res) =>{
				// 			console.log(res)
				// 			this.oldKeywordList = OldKeys; //更新历史搜索
				// 		}
				// 	});
				// }
			// };
		}
	}
}
